// src/components/planning/modals/GeneratePlanningModal.js
import React from 'react';
import { RefreshCcw } from 'lucide-react';
import ConfirmationModal from './ConfirmationModal';

const GeneratePlanningModal = ({
  isOpen,
  onClose,
  onConfirm,
  planning
}) => {
  return (
    <ConfirmationModal
      isOpen={isOpen}
      onClose={onClose}
      onConfirm={onConfirm}
      title={planning ? 'Regnrer le planning' : 'Gnrer le planning'}
      message={planning
        ? 'tes-vous sr de vouloir regnrer le planning ? Cette action remplacera le planning actuel.'
        : 'tes-vous sr de vouloir gnrer un nouveau planning ?'}
      icon={RefreshCcw}
      iconColor="#9333EA"
      iconBgColor="#F3E8FF"
      confirmButtonColor="#9333EA"
      confirmLabel="Gnrer"
    />
  );
};

export default GeneratePlanningModal;