// src/components/planning/modals/PublishPlanningModal.js
import React from 'react';
import { Upload } from 'lucide-react';
import ConfirmationModal from './ConfirmationModal';

const PublishPlanningModal = ({
  isOpen,
  onClose,
  onConfirm,
  publishedPlanning
}) => {
  return (
    <ConfirmationModal
      isOpen={isOpen}
      onClose={onClose}
      onConfirm={onConfirm}
      title="Publier le planning"
      message={publishedPlanning
        ? 'Cette action mettra  jour le planning publi. Les mdecins verront les changements immdiatement.'
        : 'Cette action publiera le planning et le rendra visible pour tous les mdecins.'}
      icon={Upload}
      iconColor="#059669"
      iconBgColor="#ECFDF5"
      confirmButtonColor="#059669"
      confirmLabel="Confirmer la publication"
    />
  );
};

export default PublishPlanningModal;